<template>
  <div class="header-news">
    <ul class="header-news-container">
      <li v-for="news in latestNews" :key="news.id">
        <LatestNewsItemWrapper :news-url="news.newsUrl">
          <span class="time" v-if="news.isDateShown">{{ news.publishedAt | dateTime }}</span>
          <span class="time" v-else>{{ news.publishedAt | time }}</span>
          <p class="news-title">{{ news.newsContent }}</p>
        </LatestNewsItemWrapper>
      </li>
    </ul>
  </div>
</template>

<script>
import LatestNewsItemWrapper from '@/components/LatestNewsItemWrapper.vue';

export default {
  name: 'LatestNews',
  components: {
    LatestNewsItemWrapper,
  },
  computed: {
    latestNews() {
      const { currentDateTime } = this.$store.state;
      const allNews = this.$store.state.latestNews;
      return allNews.filter(news => news.publishedAt <= currentDateTime).slice(0, 4);
    },
  },
};
</script>

